'use client'

import React, { useState, useEffect, useRef } from 'react'
import Image from 'next/image'

const DynamicImageSections = () => {
  const [currentSection, setCurrentSection] = useState(0)
  const [selectedImage, setSelectedImage] = useState<number | null>(null)
  const sectionsRef = useRef<HTMLDivElement[]>([])
  
  // Sample images - replace with your actual image URLs
  const images = [
    'https://www.dreamworks.com/storage/cms-uploads/the-wild-robot-keyart4.jpg',
    'https://www.dreamworks.com/storage/cms-uploads/kung-fu-panda-4-he-keyart.jpg',
    'https://www.dreamworks.com/storage/cms-uploads/OATD_Key%20Art_960x1458.jpg',
    'https://www.dreamworks.com/storage/cms-uploads/ruby-gillman-teenage-kraken-poster.jpg',
    'https://www.dreamworks.com/storage/cms-uploads/ruby-gillman-teenage-kraken-poster.jpg',
    'https://www.dreamworks.com/storage/cms-uploads/ruby-gillman-teenage-kraken-poster.jpg',
  ]

  // Handle scroll to change sections
  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY
      const windowHeight = window.innerHeight
      
      if (scrollY < windowHeight) {
        setCurrentSection(0)
      } else if (scrollY < windowHeight * 2) {
        setCurrentSection(1)
      } else if (scrollY < windowHeight * 3) {
        setCurrentSection(2)
      } else {
        setCurrentSection(3)
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  // Handle image click in carousel
  const handleCarouselImageClick = (index: number) => {
    setSelectedImage(index)
    // Scroll to bottom section
    window.scrollTo({
      top: window.innerHeight * 3,
      behavior: 'smooth'
    })
  }

  // Section 1: Stacked Images
  const StackedSection = () => (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 to-slate-800 p-8">
      <div className="relative w-80 h-96">
        {images.slice(0, 4).map((img, index) => (
          <div
            key={index}
            className="absolute transition-all duration-1000 ease-out"
            style={{
              transform: `translateX(${index * 8}px) translateY(${index * 8}px) rotate(${index * 2}deg)`,
              zIndex: images.length - index,
            }}
          >
            <div className="w-72 h-80 rounded-lg overflow-hidden shadow-2xl border-4 border-white">
              <Image
                src={img}
                alt={`Stack ${index}`}
                width={288}
                height={320}
                className="w-full h-full object-cover"
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  )

  // Section 2: Expanded Grid
  const ExpandedSection = () => (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-800 to-slate-700 p-8">
      <div className="grid grid-cols-3 gap-6 max-w-4xl">
        {images.map((img, index) => (
          <div
            key={index}
            className="group relative overflow-hidden rounded-lg shadow-lg transform transition-all duration-500 hover:scale-105"
            style={{
              animationDelay: `${index * 100}ms`,
            }}
          >
            <div className="w-64 h-48 overflow-hidden">
              <Image
                src={img}
                alt={`Expanded ${index}`}
                width={256}
                height={192}
                className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-110"
              />
            </div>
            <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-20 transition-all duration-300"></div>
          </div>
        ))}
      </div>
    </div>
  )

  // Section 3: Carousel
  const CarouselSection = () => {
    const [currentIndex, setCurrentIndex] = useState(0)
    
    useEffect(() => {
      const interval = setInterval(() => {
        setCurrentIndex((prev) => (prev + 1) % images.length)
      }, 3000)
      return () => clearInterval(interval)
    }, [])

    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-slate-700 to-slate-600 p-8">
        <div className="relative w-full max-w-6xl overflow-hidden">
          <div 
            className="flex transition-transform duration-700 ease-in-out"
            style={{
              transform: `translateX(-${currentIndex * 320}px)`,
            }}
          >
            {[...images, ...images].map((img, index) => (
              <div
                key={index}
                className="flex-shrink-0 w-80 h-60 mx-4 cursor-pointer transform transition-all duration-300 hover:scale-105"
                onClick={() => handleCarouselImageClick(index % images.length)}
              >
                <div className="w-full h-full rounded-lg overflow-hidden shadow-xl border-2 border-white/20">
                  <Image
                    src={img}
                    alt={`Carousel ${index}`}
                    width={320}
                    height={240}
                    className="w-full h-full object-cover"
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
        
        {/* Carousel indicators */}
        <div className="flex space-x-2 mt-8">
          {images.map((_, index) => (
            <button
              key={index}
              className={`w-3 h-3 rounded-full transition-all duration-300 ${
                currentIndex === index ? 'bg-white' : 'bg-white/40'
              }`}
              onClick={() => setCurrentIndex(index)}
            />
          ))}
        </div>
      </div>
    )
  }

  // Section 4: Full Screen Expanded
  const FullScreenSection = () => (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-600 to-slate-900 p-8">
      <div className="relative max-w-5xl mx-auto">
        {selectedImage !== null && (
          <div className="relative">
            <div className="w-full max-w-4xl mx-auto rounded-2xl overflow-hidden shadow-2xl">
              <Image
                src={images[selectedImage]}
                alt={`Full Screen ${selectedImage}`}
                width={1024}
                height={768}
                className="w-full h-auto object-cover"
              />
            </div>
            
            {/* Image navigation */}
            <div className="flex justify-center mt-8 space-x-4">
              {images.map((img, index) => (
                <button
                  key={index}
                  className={`w-16 h-12 rounded-lg overflow-hidden border-2 transition-all duration-300 ${
                    selectedImage === index ? 'border-white scale-110' : 'border-white/40 hover:border-white/80'
                  }`}
                  onClick={() => setSelectedImage(index)}
                >
                  <Image
                    src={img}
                    alt={`Thumb ${index}`}
                    width={64}
                    height={48}
                    className="w-full h-full object-cover"
                  />
                </button>
              ))}
            </div>
          </div>
        )}
        
        {selectedImage === null && (
          <div className="text-center text-white">
            <h2 className="text-3xl font-bold mb-4">Click an image from the carousel above</h2>
            <p className="text-xl opacity-70">Scroll up to interact with the carousel</p>
          </div>
        )}
      </div>
    </div>
  )

  return (
    <div className="relative">
      {/* Section indicators */}
      <div className="fixed right-8 top-1/2 transform -translate-y-1/2 z-50 flex flex-col space-y-4">
        {[0, 1, 2, 3].map((section) => (
          <button
            key={section}
            className={`w-4 h-4 rounded-full border-2 transition-all duration-300 ${
              currentSection === section 
                ? 'bg-white border-white' 
                : 'bg-transparent border-white/50 hover:border-white'
            }`}
            onClick={() => {
              window.scrollTo({
                top: window.innerHeight * section,
                behavior: 'smooth'
              })
            }}
          />
        ))}
      </div>

      {/* All sections */}
      <div ref={el => { if (el) sectionsRef.current[0] = el }}>
        <StackedSection />
      </div>
      
      <div ref={el => { if (el) sectionsRef.current[1] = el }}>
        <ExpandedSection />
      </div>
      
      <div ref={el => { if (el) sectionsRef.current[2] = el }}>
        <CarouselSection />
      </div>
      
      <div ref={el => { if (el) sectionsRef.current[3] = el }}>
        <FullScreenSection />
      </div>

      {/* Scroll indicator */}
      <div className="fixed bottom-8 left-1/2 transform -translate-x-1/2 text-white text-center z-50">
        <div className="animate-bounce">
          <svg className="w-6 h-6 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
          </svg>
        </div>
        <p className="mt-2 text-sm opacity-70">
          {currentSection === 0 && "Scroll to expand"}
          {currentSection === 1 && "Scroll for carousel"}
          {currentSection === 2 && "Click image to view"}
          {currentSection === 3 && "Full screen view"}
        </p>
      </div>
    </div>
  )
}

export default DynamicImageSections